import React, { Component } from 'react'
import Slideshow from './Sllides'

class Courses extends Component{
    render(){
        return(
            <div id='course' className='w-100 d-flex justify-content-center align-items-center bg-dark pt-5'>
                <div className='w-50'>
                <Slideshow />
                </div>
                <div className='w-50 pas'>
                <h1 className='text-white text-center fw-bold '> My <span className='text-danger'>Courses</span> </h1>

                </div>
                
            </div>
        )
    }
}

export default Courses