import React, { Component } from 'react'
import firstPic from '../../images/IMG_5922-removebg-preview.png';
import Typewriter from 'typewriter-effect';
import Social from './Social';

class Section1 extends Component{
    render(){
       
        return(
            <div className='w-100 bg-dark text-white h-75 position-relative d-flex justify-content-center  mt-5'>
                <div className='w-50  mt-5 ms-5 pt-5' >
                  <h1 className='fs-1 fw-bold '>Aysam <span className='text-danger'> Tohme </span> </h1>
                 <span className='fs-4 font-monospace words'>
                  <Typewriter 
                       options={{
                        autoStart: true,
                        loop: true,
                        strings: [`I'M Developer `, `I'M Freelancer `, `I'M Programer `],
                       }}

                    onInit={(typewriter) => {
                        typewriter.typeString('Freelancer')
                        .callFunction(() => {
                            console.log('String typed out!');
                        })
                        

                        .pauseFor(2500)
                        .deleteAll()
                        .callFunction(() => {
                            console.log('All strings were deleted');
                        })
                        .start();
                    }}
                    />
                    <Social />
                    </span>
                </div>

                <div className='w-50 mt-3 '>
                    <img className='img-fluid' src={firstPic}   />
                </div>
            </div>
        )
    }
}

export default Section1