import React, { Component } from 'react'
import SkillBar from 'react-skillbars';

class Skills extends Component{
    render(){

        const skills = [
            { type: 'HTML', level: 100 },
            { type: 'React', level: 90 },
            { type: 'Javascript', level: 90 },
            { type: 'CSS', level: 100 },
            { type: 'MySQL', level: 90 },
            { type: 'Node.js', level: 70 }

          ];

         
         
                      
        return(
           
            <div className='w-100 d-flex justify-content-center align-items-center bg-dark'>
                <div className='w-50'>
                    <h1 className='text-white text-center fw-bold'> My <span className='text-danger'>Skills</span> </h1>
                </div>
                <div className='w-50 text-center pt-2 text-danger pe-2'>
                     <SkillBar skills={skills}  />
                </div>
            </div>
               
        )
    }
}

export default Skills