import React from 'react';
import {
  MDBFooter,
  MDBContainer,
  MDBIcon,
  MDBInput,
  MDBCol,
  MDBRow,
  MDBBtn
} from 'mdb-react-ui-kit';
import emailjs from 'emailjs-com';





const sendEmail = (e) => {
  e.preventDefault();

  emailjs.sendForm('service_7va7nx6', 'template_dhwl7fj',e.target, 'zG1vJOB3cie28Lodw')
  .then((result) => {
      console.log(result.text);
  }, (error) => {
      console.log(error.text);
  });
};
export default function App() {
  return (
    <MDBFooter id='contact' className='text-center' color='white' bgColor='dark'>
      <MDBContainer className='p-4'>
        <section className='mb-4'>
          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-facebook-f' />
          </MDBBtn>

          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-twitter' />
          </MDBBtn>

          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-google' />
          </MDBBtn>

          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-instagram' />
          </MDBBtn>

          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-linkedin' />
          </MDBBtn>

          <MDBBtn outline color="light" floating className='m-1' href='#!' role='button'>
            <MDBIcon fab className='fa fa-github' />
          </MDBBtn>
        </section>

        <section className=''>
          <form action='' onSubmit={sendEmail}>
            <MDBRow className='d-flex justify-content-center'>
              <MDBCol size="auto">
                <p className='pt-2'>
                  <strong>Sign up for our newsletter</strong>
                </p>
              </MDBCol>

              <MDBCol md='5' start='12'>
                <MDBInput contrast type='email' placeholder='Email' className='mb-4' name='email'/>
              </MDBCol>

              <MDBCol size="auto">
                <MDBBtn outline color='light' type='submit' className='mb-4' >
                  Subscribe
                </MDBBtn>
              </MDBCol>
            </MDBRow>
          </form>
        </section>
      </MDBContainer>

      <div className='text-center p-3' style={{ backgroundColor: 'rgba(0, 0, 0, 0.2)' }}>
         2022 Copyright: 
        <a className='text-white ps-3 text-decoration-none' href='https://www.aysamtohme.tk/'>
            www.aysamtohme.tk
        </a>
      </div>
    </MDBFooter>
  );
}